、模糊，位置靠下部，孕妇同时有腹痛或阴道流血时，可能要流产。 "),_c('br'),_c('br'),_vm._v("4.胎盘胎盘是胎儿的与母亲进行营养交换与排除代谢产物的重要器官，是取代黄体的内分泌器官。胎盘的正常厚度应在2.5~5cm之间。胎盘钙化分为Ⅲ级，Ⅰ级为胎盘成熟的早期阶段，回声均匀，在怀30~32周可见此变化；Ⅱ级表示胎盘接近成熟；Ⅲ级提示胎盘已经成熟。越接近足月，胎盘越成熟，回声越不均匀。 "),_c('br'),_c('br'),_vm._v("5.羊水羊水是胎儿生长发育的场所。早期羊水来源于羊膜上皮细胞的分泌，胎儿泌尿器官形成后，胎儿的尿液为羊水的主要来源；羊水不断更新，胎儿吞饮羊水是羊水多主要去路。羊水深度在3~7cm之间为正常，超过7cm为羊水增多，少于3cm为羊水减少。 "),_c('br'),_c('br'),_vm._v("6.脐带正常情况下，脐带应漂浮在羊水中，如在胎儿颈部见到脐带影像，可能为脐带绕颈。 "),_c('br'),_c('br'),_vm._v("7.胎动有、强为正常，无、弱可能胎儿在睡眠中，也可能为异常情况，要结合其他项目综合分析。 "),_c('br'),_c('br'),_vm._v("8.股骨长度是胎儿大腿骨的长度，正常值与相应怀孕月份的BPD值差2~3cm左右，如说BPD为9.3cm，股骨长度应为7.3cm；BPD为8.9cm，股骨长度应为6.9cm等。 "),_c('br'),_c('br'),_vm._v("9.脊椎胎儿脊柱连续为正常，缺损为异常。10.脑室在妊娠14~38周正常侧脑室宽度为：7.6±0.6mm，其上限为10mm（平均值4标准差）。当宽度10~15mm为轻度增宽，其本身并无明显病理意义，约有1/3在孕期可能自行消失。大于或等于15mm，为重度侧脑室增寛或脑积水；如果没有发现其他异常，尤其是发现有神经管其他发育异常时，脑积水的可能性更大。注意的是有43%的病人会有其他结构异常或出现染色体异常时，伴有脑室异常，这类宝宝有问题的可能很大。 ")]),_c('span',{staticClass:"dialog-footer",attrs:{"slot":"footer"},slot:"footer"},[_c('el-button',{on:{"click":function($event){_vm.help = false}}},[_vm._v("关 闭")])],1)]),_c('el-dialog',{attrs:{"title":"记录孕期数据","visible":_vm.add,"width":"60%"},on:{"update:visible":function($event){_vm.add=$event}}},[_c('div',{staticStyle:{"height":"400px"}},[_c('div',{staticStyle:{"display":"flex","align-items":"center"}},[_c('span',{staticStyle:{"width":"100px"}},[_vm._v("题目：")]),_vm._v(" "),_c('el-input',{model:{value:(_vm.title),callback:function ($$v) {_vm.title=$$v},expression:"title"}})],1),_c('quillEditor',{staticStyle:{"box-sizing":"border-box","padding-top":"20px","width":"100%","height":"calc(100% - 100px)"},model:{value:(_vm.content),callback:function ($$v) {_vm.content=$$v},expression:"content"}})],1),_c('span',{staticClass:"dialog-footer",attrs:{"slot":"footer"},slot:"footer"},[_c('el-button',{on:{"click":function($event){_vm.add = false}}},[_vm._v("关 闭")]),_c('el-button',{attrs:{"type":"primary"},on:{"click":_vm.confirm}},[_vm._v("提 交")])],1)])],1)
